.container {
    text-align: center;
}

.title {
    margin-top: 50px;
    color: white;
    font-size: 60px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.title span {
    color: #26ffcb;
    padding-left: 15px;
}

.reset {
    width: 250px;
    height: 100px;
    border: none;
    cursor: pointer;
    border-radius: 50px;
    background: #1f3540;
    font-size: 26px;
    color: #26ffcb;
    margin-top: 25px;
    margin-bottom: 50px;
}
.boxes:hover {
    transform: scale(1.1); /* Membesar 10% */
    transition: transform 0.2s ease-in-out; /* Transisi smooth */
}

.boxes {
    display: flex;
    height: 180px;
   
    width: 180px;
    background: #1f3540;
    border: 4px solid #0f1b21;
    border-radius: 12px;
    cursor: pointer;
    opacity: 0; /* Awalnya tidak terlihat */
    animation: falling 0.5s ease-in-out forwards;
}

.board {
    height: 600px;
    width: 564px;
    display: flex;
    flex-wrap: wrap; /* Susunan 3x3 */
    justify-content: center;
    margin: auto;
}

.boxes img {
    margin: 50px 50px;
}

.title img {
    padding: 20px;
    height: 70px;
}

/* Animasi kotak berjatuhan */
@keyframes falling {
    0% {
        transform: translateY(-200px); /* Mulai dari atas */
        opacity: 0;
    }
    100% {
        transform: translateY(0); /* Sampai ke posisi semula */
        opacity: 1;
    }
}

/* Delay untuk tiap kotak */
.boxes:nth-child(1) {
    animation-delay: 0.1s;
}
.boxes:nth-child(2) {
    animation-delay: 0.2s;
}
.boxes:nth-child(3) {
    animation-delay: 0.3s;
}
.boxes:nth-child(4) {
    animation-delay: 0.4s;
}
.boxes:nth-child(5) {
    animation-delay: 0.5s;
}
.boxes:nth-child(6) {
    animation-delay: 0.6s;
}
.boxes:nth-child(7) {
    animation-delay: 0.7s;
}
.boxes:nth-child(8) {
    animation-delay: 0.8s;
}
.boxes:nth-child(9) {
    animation-delay: 0.9s;
}
    
/* Hover animasi untuk kotak agar membesar sedikit */
